import Endpoint from '@/services/Endpoint'

export default {
  statusItems (payload) {
    return Endpoint().get('/s1/commons/basic/status/item', {
      params: payload
    })
  },
  createStatusItem (payload) {
    return Endpoint().post('/s1/commons/basic/status/item', payload)
  },
  modifyStatusItem (payload) {
    return Endpoint().patch('/s1/commons/basic/status/item', payload)
  }
}
